.offer {
  padding: 150px 0 0;

  @include mobile {
    padding: 100px 0 0;
  }

  @include small-mobile {
    padding: 60px 0 0;
  }

  &__container {
    overflow: hidden;
  }

  &__title {
    font-weight: 600;
    font-size: 64px;
    line-height: 78px;

    @media (max-width: 1720px) {
      font-size: 46px;
      line-height: 54px;
    }

    @include mobile {
      font-size: 36px;
      line-height: 44px;
    }

    span {
      color: $whiteColor;
    }
  }

  &-item {
    max-width: 350px;
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;

    @media (min-width: 1720px) {
      max-width: 450px;
    }

    @include mobile {
      max-width: 100%;
    }

    &__img {
      border-radius: 25px;
      max-width: 450px;
      max-height: 415px;
      width: 100%;
      height: 100%;
      object-fit: cover;
      margin: 0 auto 30px;
    }

    &__cost {
      display: block;
      color: $mainColor;
      font-weight: 600;
      font-size: 32px;
      line-height: 39px;
      margin-bottom: 20px;
      text-align: center;

      @media (max-width: 1720px) {
        font-size: 26px;
        line-height: 30px;
      }

      @include small-mobile {
        font-size: 24px;
        line-height: 28px;
      }
    }

    &__links {
      display: flex;
      align-items: center;
      max-width: 380px;
      text-align: center;
      margin: 0 auto;
      justify-content: space-between;
      gap: 30px;


      @media (max-width: 1300px) {
        gap: 20px;
      }

      @include mobile {
        gap: 15px;
      }
    }

    &__link {
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
      color: rgba($whiteColor, 0.6);
      transition: all 0.4s;
      &:focus, &:hover {
        color: rgba($whiteColor, 0.9);
      }

      @media (max-width: 1720px) {
        font-size: 16px;
        line-height: 20px;
      }

      @include small-mobile {
        font-size: 14px;
        line-height: 18px;
      }
    }
  }

  &__slider {
    margin-bottom: 100px;

    @include mobile {
      margin-bottom: 50px;
    }
  }

  .swiper-wrapper {
    display: flex;
    flex-direction: row;
    align-items: first baseline;
    margin: 0 auto;
  }

  .swiper-pagination-bullets {
    margin: 100px auto 0;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;

    @include mobile {
      margin: 50px auto 0;
    }
  }

  .swiper-pagination-bullet {
    margin: 0 auto;
    background: none;
    width: 14px;
    height: 28px;
    text-align: center;
    font-size: 24px;
    line-height: 28px;
    color: rgba($whiteColor, 0.6);
    transition: color 0.3s fade-in;

    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  .swiper-pagination-bullet-active {
    color: #fff;
  }
}
